import React, { ReactNode } from 'react';
import { Menu } from '@headlessui/react';
import { isFunction } from '../../utils';

export interface DropdownItemRenderProps {
  active: boolean;
  disabled: boolean;
}

export interface DropdownItemProps {
  readonly as?: React.ElementType;
  readonly disabled?: boolean;
  readonly children: (renderProps: DropdownItemRenderProps) => ReactNode | ReactNode;
}

export const DropdownItem: React.FC<DropdownItemProps> = React.forwardRef(
  ({ as = React.Fragment, disabled, children }, ref) => (
    <Menu.Item ref={ref} as={as} disabled={disabled}>
      {isFunction(children) ? ({ active, disabled }) => children({ active, disabled }) : children}
    </Menu.Item>
  )
);

DropdownItem.displayName = 'DropdownItem';
